body {
  background: var(--chakra-colors-dash-background) !important;
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
  height: 100%;
}

::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.app {
  width: 360px;
  height: 100vh;
  min-height: 600px;
  background-color: var(--chakra-colors-light-background-primary);
  overflow: hidden;
}

/* POWER BUTTON ANIMATION */
.circle-container {
  position: relative;
  width: 300px;
  height: 300px;
}

@media (max-width: 768px) {
  .circle-container {
    width: 300px;
    height: 300px;
  }
}

.circle-container > * {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

/* Connected Status */
.circle-container.connected > * {
  border: 2px solid black;
}

/*
 * MAKE WAVE
 */

.wave {
  background: url(../assets/backgrounds/wave.svg) repeat-x;
  opacity: 0.8;
}

.wave-below {
  background-color: var(--chakra-colors-light-primary-400);
}

/*
 * SET WAVE POSITION
 */

.wave._0 {
  background-position: 0% 110%;
}
.wave-below._0 {
  clip-path: polygon(0% 110%, 0% 110%, 110% 110%, 110% 110%);
}

.wave._50 {
  background-position: 0% 57%;
}
.wave-below._50 {
  clip-path: polygon(0% 110%, 0% 58%, 110% 58%, 110% 110%);
}

.wave._100 {
  background-position: 0% 5%;
}
.wave-below._100 {
  /* clip-path: polygon(0% 120%, 0% 0%, 120% 0%, 110% 120%); */
  clip-path: polygon(0% 110%, 0% 15%, 110% 15%, 110% 110%);
  /* clip-path: polygon(0% 110%, 0% 0%, 110% 0%, 110% 110%); */
}

/*
 * DEFINE FILLING ANIMATION
 */

@keyframes fill-wave {
  from {
    background-position: -1200px 110%;
  }
}

@keyframes fill-below {
  from {
    clip-path: polygon(0% 110%, 0% 110%, 110% 110%, 110% 110%);
  }
}

/*
 * DEFINE WAVE ANIMATION
 */

@keyframes wave-0 {
  to {
    background-position: 1200px 110%;
  }
}

@keyframes wave-50 {
  to {
    background-position: 1200px 57%;
  }
}

@keyframes wave-100 {
  to {
    background-position: 1200px 5%;
  }
}

/*
 * GIVE FILLING & WAVE ANIMATION
 */

:root {
  --ease-in: cubic-bezier(0.58, 0.42, 1, 1);
  --ease-out: cubic-bezier(0, 0, 0.42, 0.58);
}

.wave {
  animation-name: fill-wave, wave-0;
  animation-duration: 3s, 5s;
  animation-delay: 0s, 3s;
  animation-fill-mode: forwards, none;
  animation-iteration-count: 1, infinite;
  animation-timing-function: var(--ease-in), var(--ease-out);
}
.wave._50 {
  animation-name: fill-wave, wave-50;
}
.wave._100 {
  animation-name: fill-wave, wave-100;
}

.wave:nth-child(2) {
  animation-duration: 5s, 6s;
}

.wave:nth-child(3) {
  animation-duration: 5s, 7s;
}

.wave-below {
  animation: 5s var(--ease-in) forwards fill-below;
}
.wave-below._50 {
  animation: 3s -2s var(--ease-in) forwards fill-below;
}
.wave-below._100 {
  animation: 4s -1s var(--ease-in) forwards fill-below;
}
